<template lang="">
  <div>
    <!-- Vue 컴포넌트에서 이벤트 처리를 할 때 v-on 디렉티브를 사용함 -->
    <!-- v-on 디렉티브는 @로 사용 가능 -->
    <!-- 클릭 이벤트는 v-on:click="메서드명" 혹은 @click="메서드명을 사용" -->
    <button type="button" @click="increaseCounter">count</button>
    <p>카운트 : {{ counter }}</p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      counter: 0,
    };
  },
  methods: {
    increaseCounter() {
      this.counter = this.counter + 1; //클릭 할 때 마다 1씩 증가
    },
  },
};
</script>
<style lang=""></style>
